import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'
import {
  withRouter,
} from 'react-router-dom'

import Table from 'material-ui/core/Table'
import TableBody from 'material-ui/core/TableBody'
import TableCell from 'material-ui/core/TableCell'
import TableHead from 'material-ui/core/TableHead'
import TableRow from 'material-ui/core/TableRow'
import Paper from 'material-ui/core/Paper'

const RootElement = styled(Paper)`
  && {
    width: 100%;
    margin-top: 15px;
    overflowX: auto;
  }
`
const MyTable = styled(Table)`
  && {
    min-width:700px;
  }
`
let id = 0

function createData(name, lastUpdateDate, categorie, logo) {
  id += 1

  return {
    id,
    name,
    lastUpdateDate,
    categorie,
    logo,
  }
}

const data = [
  createData('Mon super podcast', 1526545262579, 'LifeStyle', 'http://localhost:5000/image/logo.png'),
  createData('La vie, l\'univers et le reste', 1526545262579, 'LifeStyle', 'http://localhost:5000/image/logo.png'),
  createData('Hello World', 1526545262579, 'Science', 'http://localhost:5000/image/logo.png'),
  createData('FOOBAR podcast', 1526545262579, 'Science', 'http://localhost:5000/image/logo.png'),
  createData('The most beautifull podcast in the word', 1526545262579, 'Music', 'http://localhost:5000/image/logo.png'),
]

class PodcastList extends React.Component {
  static propTypes = {
    history: PropTypes.object.isRequired,
  }


  handleClick = (event, podcastId) => {
    console.log(podcastId)
    console.log(event)
    this.props.history.push(`/podcast/${podcastId}`)
  }
  render() {
    return (
      <RootElement>
        <MyTable>
          <TableHead>
            <TableRow>
              <TableCell> Podcast </TableCell>
              <TableCell date> Dernire Modification</TableCell>
              <TableCell> Catgory </TableCell>
              <TableCell> Image </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data.map((row) => (
              <TableRow key={row.id} onClick={(event) => this.handleClick(event, row.id)}>
                <TableCell component="th" scope="row" >
                  {row.name}
                </TableCell>
                <TableCell>{row.lastUpdateDate}</TableCell>
                <TableCell>{row.categorie}</TableCell>
                <TableCell>{row.logo}</TableCell>
              </TableRow>
              ))}
          </TableBody>
        </MyTable>
      </RootElement>
    )
  }
}

export default withRouter(PodcastList)
