import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'

import FormGroup from 'material-ui/core/FormGroup'
import Card from 'material-ui/core/Card'
import CardContent from 'material-ui/core/CardContent'
import TextField from 'material-ui/core/TextField'
import Button from 'material-ui/core/Button'
import Grid from 'material-ui/core/Grid'

const MainLoginDiv = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  height:100%;
`
const LoginButton = styled(Button)`
  margin:10px;
`

class Login extends React.Component {
  static propTypes = {
    history: PropTypes.object.isRequired,
  }

  constructor(props) {
    super(props)

    this.state = {
      login: '',
      password: '',
    }
  }

  handleLogin = (event) => {
    event.preventDefault()
    console.log(this.state.login, this.state.password)
    // if (Login(this.state.login, this.state.password)) {
    this.props.history.push('/')
    // }
  }

  handleChange = (event) => {
    this.setState({
      [event.target.name]: event.target.value,
    })
  }
  render() {
    return (
      <MainLoginDiv>
        <Grid container justify="center">
          <Card>
            <CardContent>
              <img src="http://localhost:5000/image/logo.jpg" alt="logo" />
              <form onSubmit={this.handleLogin}>
                <FormGroup>
                  <TextField
                    label="Login"
                    name="login"
                    onChange={this.handleChange}
                  />
                  <TextField
                    label="Password"
                    name="password"
                    type="password"
                    onChange={this.handleChange}
                  />
                  <LoginButton type="submit" variant="raised" color="primary" onClick={this.handleLogin}>
                      Login
                  </LoginButton>
                </FormGroup>
              </form>
            </CardContent>
          </Card>
        </Grid>
      </MainLoginDiv>
    )
  }
}

export default Login
