import {
  connect,
} from 'react-refetch'

import Podcast from './Podcast'

import config from 'client/utils/config'

export default connect((props) => ({
  getPodcast: () => ({
    podcastPromise: {
      url: `${config.get('apiHost')}/podcasts/${props.match.params.id}`,
      force: true,
      refreshing: true,
    },
  }),
  updatePodcast: (newPodcast) => ({
    url: `${config.get('apiHost')}/podcasts/${props.match.params.id}`,
    method: 'PUT',
    body: JSON.stringify(newPodcast),
    headers: {
      Authorization: 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpZCI6IjViMjY3NmYwZjM2YTU0ZGMyMTZjMjc4YSIsImlhdCI6MTUyOTI0NzUzMiwiZXhwIjoxNTM0NDMxNTMyfQ.T0RNvrqFh4ykLoQLvEPY2fs1SHs5AP8b0byor-OVwLQ',
    },
    andThen: () => ({
      podcastPromise: {
        url: `${config.get('apiHost')}/podcasts/${props.match.params.id}`,
        force: true,
        refreshing: true,
      },
    }),
  }),
  podcastPromise: `${config.get('apiHost')}/podcasts/${props.match.params.id}`,
}))(Podcast)
